body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.watch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: turn-the-arrows;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.watch__seconds {
  transform-origin: bottom;
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: turn-the-arrows;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.watch__center {
  transform-origin: bottom;
  z-index: 2;
  background-color: #f6a503;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 48.5%;
  left: 48.5%;
}

.watch--speed-up__minutes {
  animation-duration: 600s;
}

.watch--speed-up__seconds {
  animation-duration: 10s;
}

@keyframes turn-the-arrows {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

